var RpcEngine = require('json-rpc-engine');

var providerFromEngine = require('eth-json-rpc-middleware/providerFromEngine');

var createInfuraMiddleware = require('./index');

module.exports = createProvider;

function createProvider(opts) {
  var engine = new RpcEngine();
  engine.push(createInfuraMiddleware(opts));
  return providerFromEngine(engine);
}