var margin = {top: 40, right: 90, bottom: 30, left: 90},
    width = 1000 - margin.left - margin.right,
    height = 700 - margin.top - margin.bottom
function getNestedChildren(arr, parent) {
    var out = []
    for(var i in arr) {
        if(arr[i].parent == parent) {
            var children = getNestedChildren(arr, arr[i].name)

            if(children.length) {
                arr[i].children = children
            }
            out.push(arr[i])
        }
    }
    return out
}
const Referrals = () => {
    const [treeData,setTreeData] = useState(null)
    const [userInputAddress,setUserInputAddress] = useState('');
    const [loading,setLoading] = useState(false);
    const handleUserFind = async () => {
      if (!loading){
        if (web3.utils.isAddress(userInputAddress)){
          const us = await contract.methods.us(userInputAddress).call();
          if (us.x){
            setTreeData(null);
            a(userInputAddress);
          } else {
            return toast.error('Your input address not found.')
          }
        } else {
          return toast.error('Invalid address.')
        }
      }
    }
    const a = async (address) => {
        let arr = [];
        arr.push({name: address,parent: "null"});
        const b = async (add,count) => {
            if (add.length ===0) return;
            let m = [];
            for (let i of add){
                const item = await contract.methods.vUR(i).call();
                m.push(item);
            }
            const rs = await Promise.all(m);
            let n = [];
            for (let [i,v] of rs.entries()){
                n.push(...v);
                for (let z of v){
                    arr.push({name: z,parent: add[i]});
                }
            }
            if (count >=4 ){
                return;
            }
            await b(n,count+1);
        }
        await b([address],0);
        const ns = getNestedChildren(arr,"null");
        setTreeData(ns);
    }
    const handleTreeClick = async (address) => {
      window.open(`${defaultVars.linkaddress}/${address}`,"_blank")
    }
    useEffect(()=>{
        const address = Cookies.get('user');
        a(address);
        // setTreeData(datatest);
    },[null])
    useEffect(()=>{
        if (treeData && treeData[0].children){
            var treemap = d3.tree()
                .size([width, height]);
            var nodes = d3.hierarchy(treeData[0]);
            nodes = treemap(nodes);
            d3.selectAll('#d3z').select('svg').remove();
            var svg = d3.selectAll("#d3z").append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom),
                g = svg.append("g")
                    .attr("transform",
                        "translate(" + margin.left + "," + margin.top + ")");
            svg.call(d3.zoom().on('zoom',()=>{
                g.attr('transform',d3.event.transform);
            }))
            var link = g.selectAll(".link")
                .data( nodes.descendants().slice(1))
                .enter().append("path")
                .attr("class", "link")
                .attr("d", function(d) {
                    return "M" + d.x + "," + d.y
                    + "C" + d.x + "," + (d.y + d.parent.y) / 2
                    + " " + d.parent.x + "," +  (d.y + d.parent.y) / 2
                    + " " + d.parent.x + "," + d.parent.y;
                });
            var node = g.selectAll(".node")
                .data(nodes.descendants())
                .enter().append("g")
                .attr("class", function(d) { 
                    return "node" + 
                        (d.children ? " node--internal" : " node--leaf"); })
                .attr("transform", function(d) { 
                    return "translate(" + d.x + "," + d.y + ")"; })
                .on('click',d=>handleTreeClick(d.data.name));
            node.append("circle")
            .attr("r", 10);
            node.append("text")
            .attr('class','fulltext')
            .attr("dy", ".35em")
            .attr("y", function(d) { return d.children ? -20 : 20; })
            .style("text-anchor", "middle")
            .text(function(d) { return `${d.data.name}` });
            node.append("text")
            .attr('class','slicetext')
            .attr("dy", ".35em")
            .attr("y", function(d) { return d.children ? -20 : 20; })
            .style("text-anchor", "middle")
            .text(function(d) { return `${d.data.name.slice(-3)}` });
        } else {
          d3.selectAll('#d3z').select('svg').remove();
        }
    },[treeData])
    return (
        <div className="p-2 lg:p-8 bg-white shadow w-full">
            <div className="font-sans text-16 font-bold text-lights-orangebold uppercase text-center mb-8">Referrals</div>
            <div className="relative mb-4">
              <input name="searchBox" type="text" className="px-4 py-2 border w-full rounded-full focus:outline-none focus:border-lights-orangebold" placeholder="Insert address" value={userInputAddress} onChange={(e)=>setUserInputAddress(e.target.value)}/>
              <div className="absolute top-0 right-0 h-full w-12 flex items-center justify-center text-lights-orangebold cursor-pointer" onClick={handleUserFind}>
                <svg viewBox="0 0 512 512" className="w-6 h-6 fill-current">
                  <path fill="currentColor" d="M505 442.7L405.3 343c-4.5-4.5-10.6-7-17-7H372c27.6-35.3 44-79.7 44-128C416 93.1 322.9 0 208 0S0 93.1 0 208s93.1 208 208 208c48.3 0 92.7-16.4 128-44v16.3c0 6.4 2.5 12.5 7 17l99.7 99.7c9.4 9.4 24.6 9.4 33.9 0l28.3-28.3c9.4-9.4 9.4-24.6.1-34zM208 336c-70.7 0-128-57.2-128-128 0-70.7 57.2-128 128-128 70.7 0 128 57.2 128 128 0 70.7-57.2 128-128 128z"></path>
                </svg>
              </div>
            </div>
            <div className="overflow-x-scroll ref-container">
                {treeData && treeData[0] ? null : <div><Skeleton count={40}/></div>}
                <div className="" id="d3z"></div>
                {treeData && !treeData[0].children ? <div className="px-2 text-center text-lights-bluebold py-2">No referrals</div>: null}
            </div>
        </div>
    )
}

export default Referrals